body {
  color: #fff;
  background-color: #4b4847;
}

.split {
  z-index: 1;
  width: 50%;
  height: 100%;
  padding-top: 20px;
  position: fixed;
  top: 0;
  overflow-x: hidden;
}

.left {
  left: 0;
}

.right {
  right: 0;
}

.squares {
  justify-content: space-around;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.square {
  cursor: pointer;
  border-radius: 10px;
  align-items: center;
  width: 50px;
  height: 50px;
  font-weight: bold;
  display: block;
}

.square.clicked {
  border: 4px solid #0d6efd;
}

.white-square {
  background-color: #fff;
}

.white-square:hover:after {
  content: "White";
  color: #fff;
  white-space: nowrap;
  background-color: #000c;
  border-radius: 5px;
  padding: 5px 10px;
  position: absolute;
  transform: translateX(-50%);
}

.black-square {
  color: #fff;
  background-color: #000;
}

.black-square:hover:after {
  content: "Black";
  color: #fff;
  white-space: nowrap;
  background-color: #000c;
  border-radius: 5px;
  padding: 5px 10px;
  position: absolute;
  transform: translateX(-50%);
}

.half-square {
  background: linear-gradient(to bottom right, #fff 50%, #000 50%);
}

.half-square:hover:after {
  content: "Random";
  color: #fff;
  white-space: nowrap;
  background-color: #000c;
  border-radius: 5px;
  padding: 5px 10px;
  position: absolute;
  transform: translateX(-50%);
}

canvas {
  margin: auto;
  display: block;
}

.centered {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.card {
  color: #fff;
  background-color: #4e7837;
  box-shadow: 4px 8px 8px #00000061;
}

.nav-link, .nav-link:hover {
  color: #fff;
}

.ctrl-buttons {
  margin-top: 3%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

#startBtn {
  margin-right: 10%;
}
